import React, { useEffect, useState } from "react";
import config from "../../ultis/datatable";
import $ from "jquery";
import Pagination from "../../components/Pagination";
import Create from "./child/Create";
import Edit from "./child/Edit";
import Delete from "./child/Delete";
import ModalUID from "../../components/ModalUID";
export default function Department() {
  const [state, setState] = useState({
    modal : {
      componentAction: null,
      title : "",
      method : null
    }
  });

  var { modal } = state;

  return (
    <div className="container-fluid">
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Phòng - Ban</h4>{" "}
        <button
        
          className="btn btn-info btn-icon-split btn-sm"
          onClick={() =>
            setState({ ...state, modal : { componentAction: <Create></Create> , title : "Thêm phòng - ban"  , method : "CREATE"}})
          }
          data-toggle="modal"
          data-target="#modalUID"
        >
          <span className="icon text-white-50">
            <i className="fas fa-plus" />
          </span>
          <span className="text">Thêm phòng - ban</span>
        </button>
      </div>
      <div className="card shadow mb-4">
        <div className="card-header py-3">
        <div className="row" style={{ justifyContent: "space-between" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm theo khóa học"
                  
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
       
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
        
              <table
                className="table no-footer dataTable"
                width="100%"
                aria-describedby="dataTable_info"
              >
                <thead>
                  <tr>
                    <th
                      className="sorting_disabled sorting_asc"
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th className="sorting_disabled" aria-label="Họ và tên">
                      Mã phòng - ban
                    </th>
                    <th className="sorting_disabled" aria-label="Tên tài khoản">
                      Tên phòng ban
                    </th>
                    <th className="sorting_disabled" aria-label="Tên tài khoản">
                      Hành động
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="hover-product odd">
                    <td className="sorting_1">1</td>
                    <td>A303</td>
                    <td>Kỹ thuật</td>

                    <td style={{ display: "flex" }}>
                      <button
                        onClick={() =>
                          setState({ ...state, modal : { componentAction: <Edit></Edit> , title : "Sửa phòng - ban" , method : "EDIT"}})
                        }
                        className="btn btn-warning btn-sm show"
                        data-toggle="modal"
                        data-target="#modalUID"
                      >
                        <i className="fa fa-edit" /> Sửa
                      </button>
                      <button
                             onClick={() =>
                              setState({ ...state, modal : {componentAction: <Delete></Delete> , title : "Xóa phòng - ban" , method : "DELETE" }})
                            }
                        data-toggle="modal"
                        data-target="#modalUID"
                        className="btn btn-danger btn-sm show"
                        style={{ marginLeft: "10px" }}
                      >
                        <i className="fa fa-trash" /> Xóa
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <Pagination></Pagination>
        </div>
      </div>

      <ModalUID method = {modal.method} title = {modal.title}> {modal.componentAction} </ModalUID>
    </div>
  );
}
