import React, { useEffect, useState } from "react";
import ModalUID from "../../../components/ModalUID";

export default function ModalCreate() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;



  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
      <form role="form" >
        <div class="form-group">
          <label for="product_name">Mã phòng - basn</label>
          <input
            type="text"
            class="form-control"
            id="txtName_branch"
            placeholder="Nhập..."
            autocomplete="off"
          />
        </div>
        <div class="form-group">
          <label for="product_name">Tên phòng - ban</label>
          <input
            name="name"
            value={name}
            onChange={onChange}
            type="text"
            class="form-control"
            id="txtPhone_branch"
            placeholder="Nhập..."
            autocomplete="off"
          />
        </div>
      </form>
  );
}
